<template>
	<div id="order">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-ellipsis van-nav-bar__title">订单</div>
      </div>
    </div>
    <div class="login_box">
      <van-tabs v-model="active" @change="aaa">
        <van-tab title="待付款">
          <div class="background_color_ffffff" style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);margin: 10px;padding: 10px;border-radius: 5px;" v-for="(item,index) in orderList" v-if="orderList">
            <div>
              <img :src="item.shop.shoplogo" style="vertical-align: sub;height: 20px;"/>
              <span class="font_14">{{item.shop.shopname}}</span>
            </div>
            <van-card style="margin-top: 0">
              <div slot="thumb">
                <img :src="item.goods.goodslogo.split(',')[0].split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0].split(',')[0]" width="100%" style="display: block;height:63px" />
              </div>
              <div slot="tags" class="font_12 padding_bottom_5">
                {{item.goods.goodsname}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 18px;overflow: auto">
                {{item.goods.introduce}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b" style="padding-top: 12px;">
                <span>￥{{item.goods.price}}</span>
              </div>
            </van-card>
            <div class="font_12 text_right">
              共{{item.num}}件商品,合计:
              <span class="font_14" style="color: #c10804;">¥{{item.totalmoney}}</span>
            </div>
            <div class="font_12 color_8b8b8b">
              订单编号:{{item.orderid}}
            </div>
            <div class="text_right font_12">
              <button class="seeOrder" @click="cancleOrder(item)">取消订单</button>
              <button class="payment" @click="toOrderCheck(item)">立即付款</button>
            </div>
          </div>
        </van-tab>
        <van-tab title="待发货">
          <div class="background_color_ffffff" style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);margin: 10px;padding: 10px;border-radius: 5px;" v-for="(item,index) in orderList" v-if="orderList" :key="index">
            <div>
              <img :src="item.shop.shoplogo" style="vertical-align: sub;height: 20px;"/>
              <span class="font_14">{{item.shop.shopname}}</span>
            </div>
            <van-card style="margin-top: 0">
              <div slot="thumb">
                <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
              </div>
              <div slot="tags" class="font_12 padding_bottom_5">
                {{item.goods.goodsname}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 18px;overflow: auto">
                {{item.goods.introduce}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b" style="padding-top: 12px;">
                <span>￥{{item.goods.price}}</span>
              </div>
            </van-card>
            <div class="font_12 text_right">
              共{{item.num}}件商品,合计:
              <span class="font_14" style="color: #c10804;">¥{{item.totalmoney}}</span>
            </div>
            <div class="font_12 color_8b8b8b">
              订单编号:{{item.orderid}}
            </div>
            <div class="text_right font_12">
              <button class="seeOrder" v-if="item.returntype==0">退款审核中</button>
              <button class="seeOrder" v-if="item.returntype==1">退款成功</button>
              <button class="seeOrder" v-if="item.returntype==2">退款拒绝</button>
              <button class="seeOrder" @click="returnOrder(item,index)" v-else>我要退款</button>
              <button class="seeOrder" @click="toOrderInfo(item)">查看订单</button>
            </div>
          </div>
        </van-tab>
        <van-tab title="待收货">
          <div class="background_color_ffffff" style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);margin: 10px;padding: 10px;border-radius: 5px;" v-for="(item,index) in orderList" v-if="orderList">
            <div>
              <img :src="item.shop.shoplogo" style="vertical-align: sub;height: 20px;"/>
              <span class="font_14">{{item.shop.shopname}}</span>
            </div>
            <van-card style="margin-top: 0">
              <div slot="thumb">
                <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
              </div>
              <div slot="tags" class="font_12 padding_bottom_5">
                {{item.goods.goodsname}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 18px;overflow: auto">
                {{item.goods.introduce}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b" style="padding-top: 12px;">
                <span>￥{{item.goods.price}}</span>
              </div>
            </van-card>
            <div class="font_12 text_right">
              共{{item.num}}件商品,合计:
              <span class="font_14" style="color: #c10804;">¥{{item.totalmoney}}</span>
            </div>
            <div class="font_12 color_8b8b8b">
              订单编号:{{item.orderid}}
            </div>
            <div class="text_right font_12">
              <button class="payment" @click="orderSubmit(item)">确认收货</button>
            </div>
          </div>
        </van-tab>
        <van-tab title="待评价">
          <div class="background_color_ffffff" style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);margin: 10px;padding: 10px;border-radius: 5px;" v-for="(item,index) in orderList" v-if="orderList">
            <div>
              <img :src="item.shop.shoplogo" style="vertical-align: sub;height: 20px;"/>
              <span class="font_14">{{item.shop.shopname}}</span>
            </div>
            <van-card style="margin-top: 0">
              <div slot="thumb">
                <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
              </div>
              <div slot="tags" class="font_12 padding_bottom_5">
                {{item.goods.goodsname}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5">
                {{item.goods.introduce}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b" style="padding-top: 20px;">
                <span>￥{{item.goods.price}}</span>
              </div>
            </van-card>
            <div class="font_12 text_right">
              共{{item.num}}件商品,合计:
              <span class="font_14" style="color: #c10804;">¥{{item.totalmoney}}</span>
            </div>
            <div class="font_12 color_8b8b8b">
              订单编号:{{item.orderid}}
            </div>
            <div class="text_right font_12">
              <button class="seeOrder" @click="toOrderInfo(item)">查看订单</button>
              <button class="seeOrder" @click="toComment(item)">&nbsp;&nbsp;&nbsp;&nbsp;评价&nbsp;&nbsp;&nbsp;&nbsp;</button>
            </div>
          </div>
        </van-tab>
        <van-tab title="全部订单">
          <div class="background_color_ffffff" style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);margin: 10px;padding: 10px;border-radius: 5px;" v-for="(item,index) in orderList" v-if="orderList" :key="index">
            <div>
              <img :src="item.shop.shoplogo" style="vertical-align: sub;height: 20px;"/>
              <span class="font_14">{{item.shop.shopname}}</span>
            </div>
            <van-card style="margin-top: 0">
              <div slot="thumb">
                <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
              </div>
              <div slot="tags" class="font_12 padding_bottom_5">
                {{item.goods.goodsname}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 18px;overflow: auto">
                {{item.goods.introduce}}
              </div>
              <div slot="tags" class="font_12 color_8b8b8b" style="padding-top: 12px;">
                <span>￥{{item.goods.price}}</span>
              </div>
            </van-card>
            <div class="font_12 text_right">
              共{{item.num}}件商品,合计:
              <span class="font_14" style="color: #c10804;">¥{{item.totalmoney}}</span>
            </div>
            <div class="font_12 color_8b8b8b">
              订单编号:{{item.orderid}}
            </div>
            <div class="text_right font_12">
              <button class="seeOrder" @click="returnOrder(item,index)" v-if="item.status==1">我要退款</button>
              <button class="seeOrder" v-if="item.status==5&&item.returntype==0">退款审核中</button>
              <button class="seeOrder" v-if="item.status==5&&item.returntype==1">退款成功</button>
              <button class="seeOrder" v-if="item.status==5&&item.returntype==2">退款拒绝</button>
              <button class="seeOrder" @click="toOrderInfo(item)">查看订单</button>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'order',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        active:0,
        orderList: [],
			}
		},
    mounted() {
      this.getOrderList(0);
    },
		methods:{
      cancleOrder(item){
        let $this = this;
        let obj = {
          // userid : this.userId,
          orderid : item.orderid,
          // status : 5,
        };
        let qs = require('qs');
        $this.$ajax.post("order/cancleOrder", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '取消订单成功!',
              position: "bottom"
            });
            let orderList = [];
            for(let i=0;i<$this.orderList.length;i++){
              if(item.orderid != $this.orderList[i].orderid){
                orderList.push($this.orderList[i])
              }
            }
            $this.orderList = orderList;
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      returnOrder(item,index) {
        this.$dialog.confirm({
          title: '退款',
          message: '请确认您是否选择退款?'
        }).then(() => {
          // alert(111)
          this.submitReturn(item,index);
        }).catch(() => {
          // on cancel
          // alert(222)
        });
      },
      submitReturn(item,index) {
        let $this = this;
        let obj = {
          // userid : this.userId,
          orderid : item.orderid,
          // status : 5,
        };
        let qs = require('qs');
        $this.$ajax.post("order/returnOrder", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '退款申请成功,请等待审核!',
              position: "bottom"
            });
            $this.orderList[index].returntype = 0;
            $this.active = 4;
            $this.aaa();
            // let orderList = [];
            // for(let i=0;i<$this.orderList.length;i++){
            //   if(item.orderid != $this.orderList[i].orderid){
            //     orderList.push($this.orderList[i])
            //   }
            // }
            // $this.orderList = orderList;
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      toOrderCheck(dataMap){
        this.$store.commit('setOrderCheck', dataMap);
        this.$router.push('orderCheck');
        // this.$router.push({path:'orderCheck',query:{data:dataMap}});
      },
      toComment(dataMap){
        this.$router.push({path:'comment',query:{data:dataMap}});
      },
      toOrderInfo(dataMap){
        this.$router.push({path:'orderInfo',query:{data:dataMap}});
      },
      aaa(){
        // alert(this.active);
        this.getOrderList(this.active);
      },
      orderSubmit(dataMap){
        let $this = this;
        let obj = {
          userid: $this.userId,
          orderid: dataMap.orderid
        };
        let qs = require('qs');
        $this.$ajax.post("order/sureorder", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '确认收货成功!',
              position: "bottom"
            });
            $this.getOrderList(2);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      getOrderList(dataMap){
        if(dataMap=='4'){
          dataMap = '';
        }
        let $this = this;
        let obj = {
          userid: $this.userId,
          status: dataMap
        };
        let qs = require('qs');
        $this.$ajax.post("order/getorderlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.orderList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      // returnOrder() {
      //   let $this = this;
      //   let obj = {
      //     userid : this.userId,
      //     orderid : this.orderInfo.orderid,
      //     status : 5,
      //   };
      //   let qs = require('qs');
      //   $this.$ajax.post("order/returnOrder", qs.stringify(obj), {
      //     headers: {
      //       'Content-Type': 'application/x-www-form-urlencoded'
      //     }
      //   })
      //   .then(function(rpdata) {
      //     if(rpdata.data.meta.code=='200'){
      //       $this.$toast({
      //         message: '退款申请发送成功!',
      //         position: "bottom"
      //       });
      //     }else{
      //       $this.$toast({
      //         message: rpdata.data.meta.msg,
      //         position: "bottom"
      //       });
      //     }
      //   })
      //   .catch(function(err) {})
      // },
		}
	}
</script>
<style>
  #order .van-card__thumb {
    overflow: hidden !important;
  }
  #order .van-tabs__wrap {
    top: 46px !important;
    position: fixed !important;
  }
  #order .backMoney {
    border: 1px solid #8b8b8b;
    background: none;
    padding: 5px 15px;
    border-radius: 5px;
  }
  #order .van-tab--active {
    color: #c10804;
  }
  #order .van-tabs__line {
    background-color: #c10804;
  }
   #order .seeOrder{
     background: none;
     border: 1px solid #c10804;
     color: #c10804;
     border-radius: 5px;
     padding: 5px;
   }
  #order .payment{
    background: none;
    border: 1px solid #c10804;
    color: #c10804;
    border-radius: 5px;
    padding: 5px;
  }
  #order .van-card {
    height: 76px;
  }
  #order .van-card {
    padding: 5px 15px 5px 95px;
  }
  #order .van-card__thumb {
    top: 3px;
    left: 0px;
    height: 68px;
  }
  .login_box{
    margin-top: 44px;
    margin-bottom: 58px;
  }
</style>



